.video {
	position: fixed;
	left: 0;
	right: 0;
	z-index: 200;
	top: 0;
	bottom: 0;
	@include vertical;
	opacity: 0;
	overflow: auto;
	visibility: hidden;
	transition: opacity 0.3s, visibility 0.3s;
	&.open {
		visibility: visible;
		opacity: 1; }
	&__wrapp {
		width: 100%;
		vertical-align: middle;
		display: inline-block;
		max-width: 1370px;
		padding:  0 120px;
		margin: 0 auto;
		height: 100%;
		@include sm {
			padding: 0; }
		iframe {
			width: 100%;
			height: 100%; } }
	&__video {
		height: 100%; }
	&__button-close {
		position: absolute;
		top: 0;
		right: 0; }
	&__pause-play {
		position: absolute;
		z-index: 100;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		width: 94px;
		height: 66px;
		opacity: 0.9;
		background-color: #cd201f;
		border-radius: 20px;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: 0;
		visibility: hidden;
		&.opens {
			opacity: 1;
			visibility: visible; } }
	&__pause-play-icon {
		@include arr(20, 20, #fff, r); } }
