  margin-right: 10px;
}

.switch__title-to {
  margin-left: 25px;
}

.switch__icon-from {
  height: 5px;
  width: 5px;
  background-color: #fff;
  border-radius: 50%;
  padding: 15px;
  border: 1px solid #396f8c;
  position: relative;
  margin: 0 70px 0 10px;
}

.switch__icon-from:before {
  position: absolute;
  content: '';
  width: 5px;
  height: 5px;
  background-color: #396f8c;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.switch__icon-from:after {
  content: '';
  position: absolute;
  left: 33px;
  width: 64px;
  height: 1px;
  border: 1px dotted grey;
}

.switch__icon-to {
  height: 5px;
  width: 5px;
  background-color: #fff;
  border-radius: 50%;
  padding: 5px;
  border: 1px solid #396f8c;
  position: relative;
  margin-right: 10px;
}

.switch__icon-to:before {
  position: absolute;
  content: '';
  width: 5px;
  height: 5px;
  background-color: #396f8c;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

