.company {
	background: #eaeff2;
	&__title {
		margin-bottom: 20px; }
	&__breadcrumbs {
		margin-bottom: 65px; }
	&__content {
		@include clr; }
	&__row {
		display: flex;
		margin-bottom: 55px;
		justify-content: space-between;
		@include sm {
			display: block; } }
	&__col {
		display: flex;
		max-width: 320px;
		align-items: center;
		@include sm {
			margin-bottom: 40px;
			justify-content: space-between; } }
	&__number {
		font-size: 80px;
		font-weight: 700;
		line-height: 22px;
		@include sm {
			font-size: 60px; } }
	&__info {
		line-height: 22px;
		max-width: 173px;
		margin-left: 30px; }
	&__wrap {
		max-width: 1210px;
		padding: 0 40px;
		margin: 0 auto;
		@include md {
			padding: 0 20px; }
		@include clr;
		p {
			line-height: 22px;
			margin-bottom: 30px;
			@include md {
				margin-bottom: 20px; } }
		span {
			display: block;
			margin-bottom: 20px; }
		ul {
			padding-left: 13px;
			margin-bottom: 70px;
			@include md {
				margin-bottom: 35px; }
			li {
				list-style-type: none;
				position: relative;
				padding-left: 16px;
				line-height: 30px;
				@include md {
					line-height: 22px; }
				&::before {
					position: absolute;
					top: 11px;
					left: 0;
					content: '';
					width: 6px;
					height: 6px;
					background-color: #f58220;
					border-radius: 50%;
					@include md {
						top: 9px; } } } }
		img {
			float: right;
			width: 100%;
			display: block;
			max-width: 375px;
			margin-left: 81px;
			@include sm {
				float: none;
				margin: 0 auto 30px; } }
		h3 {
			margin-bottom: 35px;
			display: inline-block;
			font-size: 24px;
			font-weight: 500;
			@include md {
				font-size: 20px;
				margin-bottom: 30px; } }
		iframe {
			display: block;
			margin: 45px auto 45px;
			width: 100%;
			max-width: 730px;
			@include md {
				margin: 30px auto 30px;
				@include sm {
					height: 300px; }
				@include r(350) {
					height: 155px; } } }


		blockquote {
			padding-left: 118px;
			position: relative;
			margin-bottom: 30px;
			line-height: 22px;
			&:before {
				content: url(../img/quote.png);
				position: absolute;
				top: 0;
				left: 60px; } }
