// FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//GothamPro
//medium
@font-face {
	@include fontdefault(normal, 500, 'GothamPro');
	@include fontpath('GothamPro-Medium', 'GothamPro-Medium', 'GothamPro-Medium'); }

@font-face {
	@include fontdefault(italic, 500, 'GothamPro');
	@include fontpath('GothamPro-Medium', 'GothamPro-Medium', 'GothamPro-Medium'); }

//regular
@font-face {
	@include fontdefault(normal, 400, 'GothamPro');
	@include fontpath('GothamPro', 'GothamPro', 'GothamPro'); }

@font-face {
	@include fontdefault(italic, 400, 'GothamPro');
	@include fontpath('GothamPro-Italic', 'GothamPro-Italic', 'GothamPro-italic'); }
