.contact-page {
	background: #eaeff2;
	&__wrapp {
		max-width: 1360px;
		padding: 0 40px;
		margin: 0 auto; }
	&__breadcrumbs {
		margin-bottom: 66px; }
	&__location {
		margin-bottom: 25px; }
	&__title {
		margin-bottom: 20px; }
	&__phone {
		margin-bottom: 40px; }
	&__map {
		width: 100%; } }

.location {
	&__title {
		color: #2e5b72;
		font-size: 16px;
		font-weight: 500;
		margin-right: 25px;
		line-height: 22px; }
	&__description {
		margin-right: 35px;
		@include sm {
			display: block;
			margin: 20px 0; } }
	&__go {
		color: #396f8c;
		text-decoration: none;
		line-height: 22px;
		border-bottom: 1px solid #b5c8d3; } }

.phone {
	&__title {
		color: #2e5b72;
		font-size: 16px;
		font-weight: 500;
		margin-right: 25px;
		line-height: 22px; }
	&__number {
		@include r(400) {
			display: block;
			margin-top: 20px; } } }

.departament {
	padding: 35px 0;
	&:nth-child(2n+1) {
		background: #dee6eb; }
	&__wrapp {
		max-width: 1210px;
		margin: 0 auto;
		// display: flex
		// justify-content: space-between
		// align-items: flex-end
		padding: 0 40px;
		@include md {
			padding: 0 20px; } }
	&__title {
		margin-bottom: 35px;
		color: #2e5b72;
		font-size: 24px;
		font-weight: 500;
		@include r(1023) {
			margin-bottom: 30px; } }
	&__row {
		display: flex;
		@include sm {
			display: block; } }
	&__info-title {
	 	margin-bottom: 20px;
	 	color: #2e5b72;
	 	font-size: 16px;
	 	font-weight: 500;
	 	line-height: 22px; }
	&__info {
		color: #396f8c; }
	&__info-m {
		color: #396f8c;
		text-decoration: none;
		border-bottom: 1px solid #b5c8d3;
		margin-bottom: 7px; }
	&__col {
		width: 33.33%;
		@include r(1023) {
			margin-bottom: 30px; }
		@include sm {
			width: 100%; } } }


